.notificacion {
    color: green;
    background-color: lightgreen;
    font-size: 20px;
    padding: 0.5em;
    border: solid;
    border-radius: 0.5em;
    border-color: green;
    margin-bottom: 0.5em;
}
.error {
    color: red;
    background-color: lightsalmon;
    font-size: 20px;
    padding: 0.5em;
    border: solid;
    border-radius: 0.5em;
    border-color: red;
    margin-bottom: 0.5em;
}